declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = typeof window !== 'undefined' ? window.Telegram?.WebApp : null;

export function useTelegram() {
  const onClose = () => {
    tg?.close();
  };

  const onShare = () => {
    tg?.switchInlineQuery('Получи своё предсказание на сегодня! 🥠');
  };

  const onBuy = async () => {
    if (!tg) return false;
    
    try {
      const result = await tg.showPopup({
        title: 'Покупка предсказания',
        message: 'Стоимость предсказания - 1 Star',
        buttons: [
          { type: 'buy', text: 'Купить', params: { amount: 1 } },
          { type: 'cancel', text: 'Отмена' }
        ]
      });
      
      return result;
    } catch (error) {
      console.error('Payment error:', error);
      return false;
    }
  };

  return {
    onClose,
    onShare,
    onBuy,
    tg,
    user: tg?.initDataUnsafe?.user,
  };
}
